




































//SDK
import amap from '@/common/SDK/amap-wx.js';
export default {
	props: ['isSearchBar', 'hasBack', 'title'],
	data() {
		return {
			afterHeaderOpacity: 1, //
			headerPosition: 'fixed',
			headerTop: 0,
			statusTop: null,
			city: '--'
		};
	},
	onLoad() {



		this.amapPlugin = new amap.AMapWX({
			//KEYKEYhttp://ask.dcloud.net.cn/article/35070
			key: '7c235a9ac4e25e482614c6b8eac6fd8e'
		});
		//
		this.amapPlugin.getRegeo({
			success: data => {
				this.city = data[0].regeocodeData.addressComponent.city.replace(//g, ''); //""
			},
			fail: data => {
				console.log(data)
			}
		});
	},
	methods: {
		//
		toMsg() {
			uni.navigateTo({
				url: '/pages/message/messages'
			})
		},
		//
		toSearch() {
			uni.showToast({
				title: ''
			});
		},
		goBack() {
			uni.navigateBack()
		}
	}
}
